:root {
  --bg-primary: #0A0A0B;
  --bg-secondary: #111113;
  --bg-tertiary: #18181B;
  --bg-elevated: #1F1F23;
  --text-primary: #FAFAFA;
  --text-secondary: #A1A1AA;
  --text-tertiary: #71717A;
  --accent: #FF6154;
  --accent-hover: #FF7A6F;
  --accent-muted: rgba(255, 97, 84, 0.15);
  --accent-glow: rgba(255, 97, 84, 0.4);
  --gradient-accent: linear-gradient(135deg, #FF6154 0%, #FF8F65 100%);
  --gradient-subtle: linear-gradient(180deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0) 100%);
  --gradient-radial: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(255, 97, 84, 0.15), transparent);
  --border: rgba(255, 255, 255, 0.08);
  --border-hover: rgba(255, 255, 255, 0.15);
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 24px;
}
.light {
  --bg-primary: #FAFAFA;
  --bg-secondary: #FFFFFF;
  --bg-tertiary: #F4F4F5;
  --bg-elevated: #FFFFFF;
  --text-primary: #18181B;
  --text-secondary: #52525B;
  --text-tertiary: #A1A1AA;
  --border: rgba(0, 0, 0, 0.06);
  --border-hover: rgba(0, 0, 0, 0.12);
  --gradient-radial: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(255, 97, 84, 0.08), transparent);
}
* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  background: var(--bg-primary);
  color: var(--text-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-feature-settings: 'cv02', 'cv03', 'cv04', 'cv11';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.text-gradient {
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.text-balance {
  text-wrap: balance;
}
.hero-glow {
  position: absolute;
  top: -200px;
  left: 50%;
  transform: translateX(-50%);
  width: 800px;
  height: 600px;
  background: radial-gradient(ellipse at center, var(--accent-glow) 0%, transparent 70%);
  filter: blur(80px);
  pointer-events: none;
  opacity: 0.6;
}
.card {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  transition: all 0.2s ease;
}
.card:hover {
  border-color: var(--border-hover);
  background: var(--bg-tertiary);
}
.card-glow {
  position: relative;
}
.card-glow::before {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: inherit;
  background: linear-gradient(135deg, var(--accent-muted) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}
.card-glow:hover::before {
  opacity: 1;
}
.btn-primary {
  background: var(--gradient-accent);
  color: white;
  padding: 12px 24px;
  border-radius: var(--radius-md);
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  transition: all 0.2s ease;
  border: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  position: relative;
  overflow: hidden;
}
.btn-primary::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(255,255,255,0.2) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.2s ease;
}
.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 32px var(--accent-glow);
}
.btn-primary:hover::before {
  opacity: 1;
}
.btn-secondary {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 12px 24px;
  border-radius: var(--radius-md);
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  transition: all 0.2s ease;
  border: 1px solid var(--border);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.btn-secondary:hover {
  border-color: var(--border-hover);
  background: var(--bg-elevated);
}
.btn-ghost {
  background: transparent;
  color: var(--text-secondary);
  padding: 8px 16px;
  border-radius: var(--radius-sm);
  font-weight: 500;
  font-size: 14px;
  transition: all 0.15s ease;
  border: none;
  cursor: pointer;
}
.btn-ghost:hover {
  color: var(--text-primary);
  background: var(--accent-muted);
}
.badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background: var(--accent-muted);
  color: var(--accent);
  border-radius: 100px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.02em;
}
.badge-outline {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
}
.divider {
  height: 1px;
  background: var(--border);
  width: 100%;
}
.section {
  position: relative;
  padding: 120px 0;
}
.section-sm {
  padding: 80px 0;
}
.grid-pattern {
  background-image: linear-gradient(var(--border) 1px, transparent 1px),
    linear-gradient(90deg, var(--border) 1px, transparent 1px);
  background-size: 64px 64px;
}
.noise {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.015;
  z-index: 1000;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(16px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-8px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}
@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 20px var(--accent-muted);
  }
  50% {
    box-shadow: 0 0 40px var(--accent-glow);
  }
}
.animate-fade-in {
  animation: fadeIn 0.5s ease-out;
}
.animate-fade-in-up {
  animation: fadeInUp 0.5s ease-out;
}
.animate-slide-in {
  animation: slideIn 0.4s ease-out;
}
.animate-pulse {
  animation: pulse 2s ease-in-out infinite;
}
.animate-float {
  animation: float 4s ease-in-out infinite;
}
.animate-glow {
  animation: glow 3s ease-in-out infinite;
}
.stagger-children > * {
  animation: fadeInUp 0.5s ease-out backwards;
}
.stagger-children > *:nth-child(1) {
  animation-delay: 0ms;
}
.stagger-children > *:nth-child(2) {
  animation-delay: 50ms;
}
.stagger-children > *:nth-child(3) {
  animation-delay: 100ms;
}
.stagger-children > *:nth-child(4) {
  animation-delay: 150ms;
}
.stagger-children > *:nth-child(5) {
  animation-delay: 200ms;
}
.stagger-children > *:nth-child(6) {
  animation-delay: 250ms;
}
::selection {
  background: var(--accent-muted);
  color: var(--accent);
}
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}
::-webkit-scrollbar-thumb {
  background: var(--bg-elevated);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--text-tertiary);
}
.spinner {
  width: 18px;
  height: 18px;
  border: 2px solid rgba(255,255,255,0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 0.7s linear infinite;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.icon-container {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--accent-muted);
  border-radius: var(--radius-md);
  color: var(--accent);
  font-size: 20px;
}
.pricing-popular {
  position: relative;
  border-color: var(--accent) !important;
}
.pricing-popular::before {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: inherit;
  background: linear-gradient(135deg, var(--accent) 0%, transparent 50%);
  opacity: 0.1;
  z-index: -1;
}
input[type="tel"],
input[type="text"],
input[type="password"] {
  background: var(--bg-tertiary);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  padding: 14px 16px;
  color: var(--text-primary);
  font-size: 16px;
  width: 100%;
  transition: all 0.2s ease;
}
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="password"]:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px var(--accent-muted);
}
input::placeholder {
  color: var(--text-tertiary);
}
label {
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
}
.hint {
  color: var(--text-tertiary);
  font-size: 13px;
}
.error-box {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  color: #FCA5A5;
  padding: 12px 16px;
  border-radius: var(--radius-md);
  font-size: 14px;
}
.success-box {
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.2);
  color: #86EFAC;
  padding: 12px 16px;
  border-radius: var(--radius-md);
  font-size: 14px;
}
.info-box {
  background: var(--accent-muted);
  border: 1px solid rgba(255, 97, 84, 0.2);
  color: var(--accent-hover);
  padding: 12px 16px;
  border-radius: var(--radius-md);
  font-size: 14px;
}
